@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,700;1,300;1,600&display=swap);
* {
  margin: 0;
  padding: 0%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    height: 100vh;
    width: 100%;
    position: relative;
    background-image:
      linear-gradient(
            to right, 
            indigo, #2bd
          );
}

.wrapper {
    border: none;
    border-radius: 5px;
    padding: 30px 30px;
    top: 40%;
    left: 50%;
    transform: translate(-50% ,-40%);
    background-color: #ffffff;
    position: absolute;
}

.quote-txt h1{
    font-family: 'Nunito', sans-serif;
    font-weight: 700;
    color: #2bd
}

.quote-txt p{
    font-family: 'Nunito', sans-serif;
    font-weight: 600;
    font-style: italic;
    color: indigo;
}

.btn-sec {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: 5% 15% auto 10%;
}

.gen-btn {
    color: #2bd;
    background: #ffffff;
    font-family: 'Nunito', sans-serif;
    font-weight: 600;
    outline: none;
    cursor: pointer;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
}

.twitter-link {
    padding: 5px 10px;
    cursor: pointer;
    border: 1px solid grey;
    border-radius: 5px;
}

.twitter-link .fa {
    color: #2bd;
    font-size: 20px;
}

@media only screen and (max-width:1100px){
    .wrapper {
        top: 20%;
        left: 50%;
        transform: translate(-50% ,-20%);
    }
}

@media only screen and (max-width:700px){
    .wrapper {
        padding: 20px 20px;
        margin: auto 10%;
        top: 30%;
        left: 0%;
        transform: translate(-0% ,-30%);
    }
    .quote-txt h1{
        font-size: 1.5rem;
    }
    
    .quote-txt p{
        font-size: 1rem;
    }
    
    .btn-sec {
        width: 90%;
        margin: 15% 10% auto 5%;
    }
}
